import { Component, OnChanges, Input } from '@angular/core';
import { Question } from '../entities/question';
import { EvaluatorService } from '../evaluator/evaluator.service';
import { Possibility } from '../entities/possibility';
import {SafeStyle} from '@angular/platform-browser';

@Component({
  selector: 'app-questionnaire',
  templateUrl: './questionnaire.component.html',
  styleUrls: ['./questionnaire.component.css']
})
export class QuestionnaireComponent implements OnChanges {
  @Input() question: Question;

  chosen: Possibility;
  maxQuestions = 0;
  currentAnsweredQuestions = 0;

  constructor(private evaluator: EvaluatorService) { }
  ngOnChanges() {
    this.question.possibleAnswers.forEach(p => p.isSelected = false);
    if (this.question.type === 'input') {
      this.chosen = this.question.possibleAnswers[0];
    }

    // this.maxQuestions = this.evaluator.getMaxQuestions();
    this.currentAnsweredQuestions = this.evaluator.answeredQuestions.length + 1;
  }

  getSelected(possibility: Possibility): String {
    if (possibility.isSelected) {
      return 'selected';
    }
    return '';
  }

  getProgress(): SafeStyle {
    return (this.currentAnsweredQuestions / this.maxQuestions * 100) + '%';
  }

  getType(): String {
    if (this.question.classForHTMLElement.includes('number')) {
      return 'number';
    }
    return 'text';
  }

  onAnswerClicked(possibility: Possibility) {
    this.question.possibleAnswers.forEach(p => p.isSelected = false);
    this.maxQuestions = this.evaluator.getMaxQuestions(possibility.nextQuestion);
    this.chosen = possibility;
    this.chosen.isSelected = true;
  }

  onInputDetected(possibility: Possibility) {
    if (possibility.answerValue == null || possibility.answerValue === '') {
      this.chosen = undefined;
    } else {
      this.chosen = possibility;
    }
  }

  evaluateAnswer() {
    if (this.chosen !== undefined) {
      this.evaluator.evaluate(this.chosen);
      this.chosen = undefined;
    }
  }

}



