import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Question } from '../entities/question';
import { Observable } from 'rxjs/observable';

@Injectable()
export class HttpQuestionService {
  baseurl: String = 'http://206.189.57.185:28080/real-estate/rest/';
  // baseurl: String = 'http://localhost:28080/real-estate/rest/';
  constructor(private httpClient: HttpClient) { }

  public getQuestion(): Observable<Question> {
    return this.httpClient.get<Question>(this.baseurl + 'question');
  }

  public evaluate(evaluation: Map<number, string>): Observable<number> {
    const headers = new HttpHeaders({'Content-Type': 'application/json; charset=utf-8'});
    const mapStr = JSON.stringify(Array.from(evaluation.entries()));
    return this.httpClient
      .post<number>(
        this.baseurl + 'question',
        '{ \"answers\": ' + mapStr + ' }',
        { headers: headers }
        );
  }
}



