import { Injectable } from '@angular/core';
import { Possibility } from '../entities/possibility';
import { Observable } from 'rxjs/observable';
import { Subject } from 'rxjs/subject';
import { Question } from '../entities/question';

@Injectable()
export class EvaluatorService {

  private newQuestionSubject: Subject<Question> = new Subject();
  public newQuestionObservable: Observable<Question> = this.newQuestionSubject.asObservable();

  public answered: Map<number, string> = new Map<number, string>();
  selectedQuestion: Question;
  public answeredQuestions: Array<Question> = [];

  constructor() { }

  public nextQuestion(question: Question) {
    this.selectedQuestion = question;
    this.newQuestionSubject.next(this.selectedQuestion);
  }

  public evaluate(possibility: Possibility) {
    this.answered.set(this.selectedQuestion.questionId, possibility.answerValue);
    this.answeredQuestions[this.answeredQuestions.length] = this.selectedQuestion;
    if (possibility.nextQuestion != null) {
      this.nextQuestion(possibility.nextQuestion);
    } else {
      this.newQuestionSubject.complete();
    }
  }

  public goBack() {
    if (this.answeredQuestions.length > 0) {
      this.selectedQuestion = this.answeredQuestions[this.answeredQuestions.length - 1];
      this.newQuestionSubject.next(this.selectedQuestion);
      this.answeredQuestions.splice(this.answeredQuestions.length - 1, 1);
    }
  }

  public getMaxQuestions(nextQuestion: Question = this.selectedQuestion): number {
    let tmp = nextQuestion;
    let numberOfQuestions = this.answered.size + 1;
    while (tmp != null) {
      tmp = tmp.possibleAnswers[0].nextQuestion;
      numberOfQuestions++;
    }
    return numberOfQuestions;
  }

}



