import { Component, OnInit } from '@angular/core';
import { Question } from './entities/question';
import { EvaluatorService } from './evaluator/evaluator.service';
import { HttpQuestionService } from './http-question/http-question.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';

  questionnaireInProgress = false;
  questionnaireFinished = false;
  selectedQuestion: Question;
  evaluatedValue = 0;
  constructor(private evaluator: EvaluatorService, private httpQuestionService: HttpQuestionService) {}

  ngOnInit() {
  }

  public onStartButtonClicked() {
    this.evaluator.newQuestionObservable.subscribe(
      {
        next: data => {
          this.selectedQuestion = data;
          this.questionnaireInProgress = true;
        },
        complete: () => {
          this.questionnaireFinished = true;
          this.questionnaireInProgress = false;
          this.httpQuestionService.evaluate(this.evaluator.answered).subscribe(data => this.evaluatedValue = data);
        },
        error: () => { this.selectedQuestion = null; }
      }
    );
    this.httpQuestionService.getQuestion().subscribe(question => this.evaluator.nextQuestion(question));
  }
}



